	module.exports = {"data":{"markdownRemark":{"html":"<p>CSS Tips &#x26; Tricks</p>\n<p><b>By</b> Josue Plaza</p>\n<p><b>Time Length</b>: 1 Hour, 20 minutes</p>\n<h3>Instructor Priorities</h3>\n<ul>\n<li>Participants should feel completely welcome and encouraged to open up and ask questions.</li>\n</ul>\n<hr>\n<h3>Prerequisites</h3>\n<ul>\n<li>A laptop with a web browser installed. (Chrome or Firefox is preferred, but Safari will do. NO INTERNET EXPLORER)</li>\n</ul>\n<h3>Workshop Description</h3>\n<p>The first languages every web developer learns are HTML and CSS. However, without proper guidance, learning these languages can be daunting.</p>\n<p>In this 2-hour workshop, let Valley Tech teach you HTML and CSS the right way! Regardless if you are interested in building your own web applications, or looking to change careers, this workshop is your (free) ticket into the developer world.</p>\n<p>You will dive head first into common CSS concepts, skill up with live coding exercises, and by the end of the day you’ll have a good handle on HTML/CSS practices. Afterward, l will point you to online coding resources, and you can stay to hear more about our amplified 12-week courses in development.</p>\n<p>Takeaways from the workshop are:</p>\n<ul>\n<li>Syntax of HTML and CSS</li>\n<li>CSS Animations</li>\n<li>Common types of websites including landing pages and marketing sites</li>\n<li>Principles of front-end code organization and project structure</li>\n<li>CSS Flexbox</li>\n<li>Bootstrap 4</li>\n</ul>\n<p><em>COME PREPARED</em></p>\n<p>This workshop is for beginners!</p>\n<p>You <em>must</em> bring along a laptop.</p>\n<h3>1. Valley Tech Intro (6:35 - 6:40)</h3>\n<h3>2. HTML Fundamentals (6:40-6:50)</h3>\n<p>Talk about HTML structure\n[Stop for Questions]</p>\n<h3>3. Introduce CSS: Syntax (6:50-7:00)</h3>\n<p>[Stop for questions]</p>\n<h3>4. CSS Essentials: Classes &#x26; Ids (7:00 - 7:10)</h3>\n<h3>5. BREAK TIME (7:10 - 7:20)</h3>\n<h3>6. Trick: Google Developer Console (Activity 1) (7:20 - 7:40)</h3>\n<h3>7. Trick: CSS Bootstrap (7:40 - 8)</h3>\n<h3>8. Activity 2: Building with Bootstrap (8 - 8:30)</h3>","headings":[{"depth":3,"value":"Instructor Priorities"},{"depth":3,"value":"Prerequisites"},{"depth":3,"value":"Workshop Description"},{"depth":3,"value":"1. Valley Tech Intro (6:35 - 6:40)"},{"depth":3,"value":"2. HTML Fundamentals (6:40-6:50)"},{"depth":3,"value":"3. Introduce CSS: Syntax (6:50-7:00)"},{"depth":3,"value":"4. CSS Essentials: Classes & Ids (7:00 - 7:10)"},{"depth":3,"value":"5. BREAK TIME (7:10 - 7:20)"},{"depth":3,"value":"6. Trick: Google Developer Console (Activity 1) (7:20 - 7:40)"},{"depth":3,"value":"7. Trick: CSS Bootstrap (7:40 - 8)"},{"depth":3,"value":"8. Activity 2: Building with Bootstrap (8 - 8:30)"}],"frontmatter":{"title":"CSS Tips & Tricks!"}}},"pathContext":{"slug":"/courses/css-tips-and-tricks/syllabus/"}}

